.sidebar {
  ul {
    padding: 0;
    margin: 0;
    text-decoration: none;
    li, ol {
      list-style: none;
    }
  }
  .widget {
    box-shadow: 0 6px 16px 0 rgba(black, .15);
    margin-bottom: 40px;
    ul, div {
      padding: 28px 40px;
      background: #fff;
      @include b(tablet) {
        padding: 14px 15px;
      }
    }
    .widgettitle {
      font-size: 20px;
      font-weight: 500;
      font-family: 'Roboto', sans-serif;
      line-height: 36px;
      color: #555;
      padding: 34px 40px 20px;
      margin: 0;
      background: #f5f5f5;
      @include b(tablet) {
        padding: 14px 15px 10px;
      }
    }

    &, .widget_recent_entries {
      ul {
        list-style: none;
        margin: 0;
        li {
          padding: 0;
          margin: 0 0 26px;
          @include b(tablet) {
            margin: 0 0 14px;
          }
          a {
            display: inline;
            font-family: 'SF UI Display', sans-serif;
            font-weight: 400;
            font-size: 16px;
            line-height: 26px;
            color: #424242;
            border-bottom: 1px dashed #424242;
            &:hover, &:focus {
              text-decoration: none;
              border-bottom-style: solid;
            }
          }
        }
      }
    }
  }

}

a.tag-cloud-link {
  display: inline-block;
  white-space: nowrap;
  max-width: 100%;
  overflow: hidden;
  text-overflow: ellipsis;
  border: 0;
  color: #777777;
  text-decoration: none;
  font-family: 'SF UI Display', sans-serif;
  font-weight: 400;
  padding: 5px;
  margin: 5px 0 0 5px;
  line-height: 100%;
  background: #eeeeee;
  &:hover, &:focus {
    color: #777777;
    text-decoration: none;
    background: darken(#eee, 5%);
    border: 0;
  }
}