* {
  outline: none !important;
  outline: 0 !important;
}

body {
  font-family: 'Roboto', sans-serif !important;
  font-weight: 400;
  font-size: 16px;
  line-height: 26px;
  color: #424242;
}

.main, .article {

  a {
    font-family: 'SF UI Display', sans-serif;
    font-weight: 400;
    color: #424242;
    border-bottom: 1px dashed #424242;
    &:hover, &:focus {
      text-decoration: none !important;
      border-bottom-style: solid;
      color: #424242;
    }
  }
  &, & p, & ul, & ol {
    font-family: 'SF UI Display', sans-serif;
    font-size: 16px;
    line-height: 24px;
    padding: 0 0 1em;
    margin: 0;
    color: #777777;
  }
  h1 {
    font-weight: 400;
    font-size: 50px;
    line-height: 60px;
    color: #424242;
    margin: 0;
    padding: 44px 0 23px;
    @include b(tablet) {
      font-size: 30px;
      line-height: 38px;
      margin: 15px 0;
    }
  }
  h2 {
    font-size: 28px;
    line-height: 36px;
    font-family: 'Roboto', sans-serif;
    font-weight: 400;
    color: #424242;
    padding: .5em 0;
    @include b(tablet) {
      font-size: 22px;
      line-height: 30px;
      margin: 0 !important;
    }
  }
  ol, ul {
    list-style-position: inside;
  }
  &__content img {
    display: block;
    max-width: 100%;
    margin: 1em auto;
    height: auto;
  }
  blockquote {
    font-size: 22px;
    font-weight: 500;
    font-family: 'Roboto', sans-serif;
    line-height: 32px;
    text-align: left;
    font-style: italic;
    color: #555;
    border: 0;
    padding: .5em 50px 1em 100px;
    margin: 0;
    @include b(mobile) {
      padding: .5em 15px 1em;
    }
    &:before, &:after {
      display: none;
    }
    p {
      padding: 0;
      margin: 0;
      font: inherit;
      line-height: inherit;
      color: inherit;
    }
  }
}