.popup-wrap {
  display: flex !important;
  position: fixed;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  z-index: 9999;
  opacity: 0;
  visibility: hidden;
  background: rgba(0, 0, 0, .25);
  transition: all .4s ease-in-out;

  .popup {
    width: 500px;
    max-width: 100%;
    max-height: 100%;
    overflow-y: auto;
    margin: auto;
    background: #fff;
    padding: 30px;
    position: relative;
    box-shadow: 0 6px 12px rgba(0, 0, 0, .175);
    top: -150%;
    transform: translateY(-50%);
    transition: all .4s ease-in-out;

    .actions-block {
      margin: 25px auto 10px;
      text-align: center;

      .action-link {
        margin: auto 15px;
        font-weight: 500;
        font-size: 22px;

        @media (max-width: 480px) {
          font-size: 16px;
        }
      }
    }
  }

  &.show {
    opacity: 1;
    visibility: visible;

    .popup {
      top: 50%;
    }
  }

  .close-btn {
    display: block;
    color: #C3C3C3;
    position: absolute;
    top: 25px;
    right: 25px;
    height: 30px;
    width: 30px;
    font-size: 30px;
    line-height: 30px;
    text-align: center;
    cursor: pointer;
    text-decoration: none;
    border: 0;
  }
  h3 {
    text-align: center;
  }
  .popup-link {
    color: #287EF5;
    font-weight: 400;
    font-size: 16px;
    @media (max-width: 767px) {
      font-size: 12px;
    }
  }
}
